@import "global.css";

pre, code[class*="language-"],
pre[class*="language-"] {
  text-align: left;
  white-space: pre-wrap;
  word-break: break-word;
  word-spacing: normal;
  color: #738a94;
  background: #f3f8fa;
  font-family: Roboto Mono, monospace;
  font-size: 1.5rem;
  line-height: 1.5em;
  border: none !important;
  tab-size: 2;
  hyphens: none;
}

:not(pre)>code[class*="language-"] {
  white-space: normal;
  border-radius: 0.2em;
  padding: 0.1em;
}

pre[class*="language-"] {
  overflow: auto;
  position: relative;
  margin: 0.5em 0;
  font-size: 1.7rem;
  padding: 1.25em 1em;
}

.language-css>code,
.language-sass>code,
.language-scss>code {
  color: #738a94;
}

[class*="language-"] .namespace {
  opacity: 0.7;
}

